import { NavConfig } from "@config/navigation/config"
import Container from "@mui/material/Container"
import Toolbar from "@mui/material/Toolbar"
import Typography from "@mui/material/Typography"
import React from "react"
import {
  ContentFiller,
  DrawerContainer,
  LogoWrapper,
  NavAppBar,
  NavDrawer,
  NavLinkButton,
  NavMenuItemsCollapsedButtonContainer,
  NavMenuItemsContainer,
  NavMenuLogoContainer
} from "./styles/styles"
import { NavbarComponent } from "./types"
import { IconButton } from "@mui/material"
import MenuIcon from "@mui/icons-material/Menu"
import CloseIcon from "@mui/icons-material/Close"
import NavMenuLogo from "@assets/image/ssso-logo-dark.png"

const Component: React.FC<NavbarComponent> = ({
  navbarList,
  drawer: { open, toggleDrawer }
}: NavbarComponent) => {
  const drawerComponent = React.useMemo(
    () => (
      <DrawerContainer>
        {navbarList.map((item) => (
          <NavLinkButton to={NavConfig[item].path} className="nav-links">
            <Typography variant="h2Bold">{NavConfig[item].label}</Typography>
          </NavLinkButton>
        ))}
        <IconButton className="close-icon" onClick={toggleDrawer.bind(this)}>
          <CloseIcon />
        </IconButton>
      </DrawerContainer>
    ),
    [NavConfig, navbarList]
  )

  return (
    <React.Fragment>
      <NavAppBar>
        <Container>
          <Toolbar disableGutters>
            <NavMenuLogoContainer>
              <LogoWrapper>
                <img src={NavMenuLogo} alt="Logo" className="nav-logo-img" />
              </LogoWrapper>
              <NavMenuItemsContainer>
                {navbarList.map((item) => (
                  <NavLinkButton to={NavConfig[item].path}>
                    <Typography variant="h4Medium">
                      {NavConfig[item].label}
                    </Typography>
                  </NavLinkButton>
                ))}
              </NavMenuItemsContainer>
              <NavMenuItemsCollapsedButtonContainer>
                <IconButton onClick={toggleDrawer.bind(this)}>
                  <MenuIcon />
                </IconButton>
              </NavMenuItemsCollapsedButtonContainer>
            </NavMenuLogoContainer>
            <nav>
              <NavDrawer
                variant="temporary"
                open={open}
                onClose={toggleDrawer}
                ModalProps={{
                  keepMounted: true // Better open performance on mobile.
                }}
              >
                {drawerComponent}
              </NavDrawer>
            </nav>
          </Toolbar>
        </Container>
      </NavAppBar>
      <ContentFiller></ContentFiller>
    </React.Fragment>
  )
}
export default Component
