import React from "react"
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide"
import { generateSlides } from "@utils/slide"
import { pxToRem } from "@utils/styles"
import SaiImage from "@assets/image/Swami.png"
import { FullViewSlideWrapper, SlideContentFullView } from "./styled"
import { FullViewCarouselType } from "./types"
import { IconButton, Typography } from "@mui/material"
import ArrowForwardIcon from "@mui/icons-material/ArrowForward"
import { addZeroPrefix } from "@utils/number"

const Component: React.FC<FullViewCarouselType> = ({
  perPage = 1,
  height = 300,
  rewind = true,
  easing = "linear",
  cover = true,
  width = "100%",
  gap = 0,
  hasTrack = false
}: FullViewCarouselType) => {
  return (
    <FullViewSlideWrapper>
      <Splide
        options={{
          perPage,
          height: pxToRem(height),
          rewind,
          easing,
          cover,
          width,
          gap: pxToRem(gap),
          pagination: false
        }}
        hasTrack={hasTrack}
        aria-labelledby="basic-example-heading"
      >
        <SplideTrack>
          {generateSlides().map((slide, index) => (
            <SplideSlide key={slide.src}>
              <SlideContentFullView
                style={{
                  backgroundImage: `url(${SaiImage})`
                }}
              >
                <div className="container text-wrapper">
                  <Typography
                    variant="bannerBold"
                    component={"div"}
                    color="primary.light"
                  >
                    Service to Man is Service to God
                  </Typography>
                  <Typography
                    variant="h3Medium"
                    component={"div"}
                    color="primary.light"
                  >
                    Serving Humanity Selflessly Reflects Divine Love, As Helping
                    Others Fulfills Spiritual Purpose And Honors God.
                  </Typography>
                </div>
                <div className="container pagination-wrapper">
                  <div className="pagination-content">
                    <Typography variant="h4Medium" color="primary.light">
                      {addZeroPrefix(index + 1)}/
                      {addZeroPrefix(generateSlides()?.length)}
                    </Typography>
                    <div className="splide__arrows">
                      <IconButton className="splide__arrow splide__arrow--prev">
                        <ArrowForwardIcon />
                      </IconButton>
                      <IconButton className="splide__arrow splide__arrow--next">
                        <ArrowForwardIcon />
                      </IconButton>
                    </div>
                  </div>
                </div>
              </SlideContentFullView>
            </SplideSlide>
          ))}
        </SplideTrack>
      </Splide>
    </FullViewSlideWrapper>
  )
}

export default Component
