import React from "react"
import { Splide, SplideSlide } from "@splidejs/react-splide"
import { generateSlides } from "@utils/slide"
import { pxToRem } from "@utils/styles"
import SaiImage from "@assets/image/sai-baba-2.png"
import { BasicSlideWrapper } from "./styled"
import { BasicCarouselType } from "./types"

const Component: React.FC<BasicCarouselType> = ({
  perPage = 1,
  height = 300,
  rewind = true,
  easing = "linear",
  cover = true,
  width = "100%",
  gap = 0
}: BasicCarouselType) => {
  return (
    <BasicSlideWrapper>
      <Splide
        options={{
          perPage,
          height: pxToRem(height),
          rewind,
          easing,
          cover,
          width,
          gap: pxToRem(gap)
        }}
        aria-labelledby="basic-example-heading"
      >
        {generateSlides().map((slide) => (
          <SplideSlide key={slide.src}>
            <img src={SaiImage} alt={slide.alt} />
          </SplideSlide>
        ))}
      </Splide>
    </BasicSlideWrapper>
  )
}

export default Component
